<template>
<div :style='{"width":"100%","padding":"30px 7% 40px","margin":"10px auto","position":"relative","background":"none","display":"block"}'>
    <el-form
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="150px"
    >
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="wenwubianhao">
              <el-input v-model="ruleForm.wenwubianhao" placeholder="" readonly></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="wenwumingcheng">
            <el-input v-model="ruleForm.wenwumingcheng" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" v-if="type!='cross' || (type=='cross' && !ro.wenwutupian)" prop="wenwutupian">
            <file-upload
            tip=""
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.wenwutupian?ruleForm.wenwutupian:''"
            @change="wenwutupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' class="upload" v-else label="" prop="wenwutupian">
                <img v-if="ruleForm.wenwutupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.wenwutupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.wenwutupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}'  label="" prop="wenwuleixing">
            <el-select v-model="ruleForm.wenwuleixing" placeholder=""  >
              <el-option
                  v-for="(item,index) in wenwuleixingOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="wenwuziliao">
            <file-upload
            tip=""
            action="file/upload"
            :limit="1"
            :multiple="true"
            :fileUrls="ruleForm.wenwuziliao?ruleForm.wenwuziliao:''"
            @change="wenwuziliaoUploadChange"
            ></file-upload>
          </el-form-item>  
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="shouzangyaoqiu">
            <el-input v-model="ruleForm.shouzangyaoqiu" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="fengcunweizhi">
            <el-input v-model="ruleForm.fengcunweizhi" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="fengcunyuanyin">
            <el-input v-model="ruleForm.fengcunyuanyin" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" v-if="type!='cross' || (type=='cross' && !ro.fengcuntupian)" prop="fengcuntupian">
            <file-upload
            tip=""
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.fengcuntupian?ruleForm.fengcuntupian:''"
            @change="fengcuntupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' class="upload" v-else label="" prop="fengcuntupian">
                <img v-if="ruleForm.fengcuntupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.fengcuntupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.fengcuntupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="fengcunshijian" >
              <el-date-picker
                  value-format="yyyy-MM-dd HH:mm:ss"
                  v-model="ruleForm.fengcunshijian" 
                  type="datetime"
                  placeholder="">
              </el-date-picker>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="yonghuzhanghao">
            <el-input v-model="ruleForm.yonghuzhanghao" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="yonghuxingming">
            <el-input v-model="ruleForm.yonghuxingming" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px 0","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(178,213,232,1) 100%)"}' label="" prop="wenwujieshao">
            <el-input
              type="textarea"
              :rows="8"
              placeholder=""
              v-model="ruleForm.wenwujieshao">
            </el-input>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"20px 0 0","alignItems":"center","justifyContent":"center","display":"flex"}'>
        <el-button :style='{"border":"2px solid #2886bc","cursor":"pointer","padding":"0 35px","boxShadow":"inset 2px 0px 12px 0px #c2ddeb","margin":"0 10px","color":"#2886bc","outline":"none","borderRadius":"8px","background":"#fff","width":"auto","lineHeight":"44px","fontSize":"16px","height":"48px"}'  type="primary" @click="onSubmit"></el-button>
        <el-button :style='{"border":"2px solid #ccc","cursor":"pointer","padding":"0 35px","boxShadow":"inset 0px 0px 12px 0px #eee","margin":"010px","color":"#999","outline":"none","borderRadius":"8px","background":"#fff","width":"auto","lineHeight":"44px","fontSize":"16px","height":"48px"}' @click="back()"></el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        baseUrl: '',
        ro:{
            wenwubianhao : false,
            wenwumingcheng : false,
            wenwutupian : false,
            wenwuleixing : false,
            wenwujieshao : false,
            wenwuziliao : false,
            shouzangyaoqiu : false,
            fengcunweizhi : false,
            fengcunyuanyin : false,
            fengcuntupian : false,
            fengcunshijian : false,
            yonghuzhanghao : false,
            yonghuxingming : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          wenwubianhao: this.getUUID(),
          wenwumingcheng: '',
          wenwutupian: '',
          wenwuleixing: '',
          wenwujieshao: '',
          wenwuziliao: '',
          shouzangyaoqiu: '',
          fengcunweizhi: '',
          fengcunyuanyin: '',
          fengcuntupian: '',
          fengcunshijian: '',
          yonghuzhanghao: '',
          yonghuxingming: '',
        },
        wenwuleixingOptions: [],
        rules: {
          wenwubianhao: [
          ],
          wenwumingcheng: [
          ],
          wenwutupian: [
          ],
          wenwuleixing: [
          ],
          wenwujieshao: [
          ],
          wenwuziliao: [
          ],
          shouzangyaoqiu: [
          ],
          fengcunweizhi: [
          ],
          fengcunyuanyin: [
          ],
          fengcuntupian: [
          ],
          fengcunshijian: [
          ],
          yonghuzhanghao: [
          ],
          yonghuxingming: [
          ],
        },
      };
    },
    computed: {



    },
    created() {
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 
      download(file){
        window.open(`${file}`)
      },
      // 
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='wenwubianhao'){
              this.ruleForm.wenwubianhao = obj[o];
              this.ro.wenwubianhao = true;
              continue;
            }
            if(o=='wenwumingcheng'){
              this.ruleForm.wenwumingcheng = obj[o];
              this.ro.wenwumingcheng = true;
              continue;
            }
            if(o=='wenwutupian'){
              this.ruleForm.wenwutupian = obj[o].split(",")[0];
              this.ro.wenwutupian = true;
              continue;
            }
            if(o=='wenwuleixing'){
              this.ruleForm.wenwuleixing = obj[o];
              this.ro.wenwuleixing = true;
              continue;
            }
            if(o=='wenwujieshao'){
              this.ruleForm.wenwujieshao = obj[o];
              this.ro.wenwujieshao = true;
              continue;
            }
            if(o=='wenwuziliao'){
              this.ruleForm.wenwuziliao = obj[o];
              this.ro.wenwuziliao = true;
              continue;
            }
            if(o=='shouzangyaoqiu'){
              this.ruleForm.shouzangyaoqiu = obj[o];
              this.ro.shouzangyaoqiu = true;
              continue;
            }
            if(o=='fengcunweizhi'){
              this.ruleForm.fengcunweizhi = obj[o];
              this.ro.fengcunweizhi = true;
              continue;
            }
            if(o=='fengcunyuanyin'){
              this.ruleForm.fengcunyuanyin = obj[o];
              this.ro.fengcunyuanyin = true;
              continue;
            }
            if(o=='fengcuntupian'){
              this.ruleForm.fengcuntupian = obj[o].split(",")[0];
              this.ro.fengcuntupian = true;
              continue;
            }
            if(o=='fengcunshijian'){
              this.ruleForm.fengcunshijian = obj[o];
              this.ro.fengcunshijian = true;
              continue;
            }
            if(o=='yonghuzhanghao'){
              this.ruleForm.yonghuzhanghao = obj[o];
              this.ro.yonghuzhanghao = true;
              continue;
            }
            if(o=='yonghuxingming'){
              this.ruleForm.yonghuxingming = obj[o];
              this.ro.yonghuxingming = true;
              continue;
            }
          }
        }
        // 
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
                this.ruleForm.yonghuzhanghao = json.yonghuzhanghao
            }
            if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
                this.ruleForm.yonghuxingming = json.yonghuxingming
            }
          }
        });
        this.$http.get('option/wenwuleixing/wenwuleixing', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.wenwuleixingOptions = res.data.data;
          }
        });
      },

    // 
      // 
      info(id) {
        this.$http.get('wenwufengcun/detail/${id}', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 
      onSubmit() {

        //
        var crossuserid;
        var crossrefid;
        var crossoptnum;
        this.$refs["ruleForm"].validate(valid => {
          if(valid) {
            if(this.type=='cross'){
                 var statusColumnName = localStorage.getItem('statusColumnName');
                 var statusColumnValue = localStorage.getItem('statusColumnValue');
                 if(statusColumnName && statusColumnName!='') {
                     var obj = JSON.parse(localStorage.getItem('crossObj'));
                     if(!statusColumnName.startsWith("[")) {
                         for (var o in obj){
                             if(o==statusColumnName){
                                 obj[o] = statusColumnValue;
                             }
                         }
                         var table = localStorage.getItem('crossTable');
                         this.$http.post(table+'/update', obj).then(res => {});
                     } else {
                            crossuserid=Number(localStorage.getItem('userid'));
                            crossrefid=obj['id'];
                            crossoptnum=localStorage.getItem('statusColumnName');
                            crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                     }
                 }
            }
            if(crossrefid && crossuserid) {
                 this.ruleForm.crossuserid=crossuserid;
                 this.ruleForm.crossrefid=crossrefid;
                 var params = {
                     page: 1,
                     limit: 10,
                     crossuserid:crossuserid,
                     crossrefid:crossrefid,
                 }
                 this.$http.get('wenwufengcun/list', {
                  params: params
                 }).then(res => {
                     if(res.data.data.total>=crossoptnum) {
                         this.$message({
                          message: localStorage.getItem('tips'),
                          type: 'success',
                          duration: 1500,
                         });
                          return false;
                     } else {
                         // 


                          this.$http.post('wenwufengcun/add', this.ruleForm).then(res => {
                              if (res.data.code == 0) {
                                  this.$message({
                                      message: '',
                                      type: 'success',
                                      duration: 1500,
                                      onClose: () => {
                                          this.$router.go(-1);
                                      }
                                  });
                              } else {
                                  this.$message({
                                      message: res.data.msg,
                                      type: 'error',
                                      duration: 1500
                                  });
                              }
                          });
                     }
                 });
             } else {


                  this.$http.post('wenwufengcun/add', this.ruleForm).then(res => {
                     if (res.data.code == 0) {
                          this.$message({
                              message: '',
                              type: 'success',
                              duration: 1500,
                              onClose: () => {
                                  this.$router.go(-1);
                              }
                          });
                      } else {
                          this.$message({
                              message: res.data.msg,
                              type: 'error',
                              duration: 1500
                          });
                      }
                  });
             }
          }
        });
      },
      // uuid
      getUUID () {
        return new Date().getTime();
      },
      // 
      back() {
        this.$router.go(-1);
      },
      wenwutupianUploadChange(fileUrls) {
          this.ruleForm.wenwutupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
      wenwuziliaoUploadChange(fileUrls) {
          this.ruleForm.wenwuziliao = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
      fengcuntupianUploadChange(fileUrls) {
          this.ruleForm.fengcuntupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
	.el-date-editor.el-input {
		width: auto;
	}
	
	.add-update-preview .el-form-item /deep/ .el-form-item__label {
	  padding: 0 15px;
	  color: #2886bc;
	  font-weight: 500;
	  width: 150px;
	  font-size: 16px;
	  line-height: 40px;
